const tagsview ={
    state:{
        visitedviews:[
        ],//
    },
    mutations:{//
        ADD_VISITED_VIEWS:(state,view)=>{//--
            if(state.visitedviews.some(v=>v.path==view.path))return;
            if (view.name == 'login'){
                return ;
            } 
            if(view.name == 'admin')return;
            state.visitedviews.push({
                name:view.name,
                path:view.path,
                title:view.meta.title || 'no-title',
                icon:view.meta.icon || 'el-icon-setting'
            })
        },
        DEL_VISITED_VIEWS:(state, view)=>{//--
            for(let [i,v] of state.visitedviews.entries()){
                if(v.path == view.path && view.name != 'admin'){
                    state.visitedviews.splice(i,1);
                    break
                }
            }
        },
        // 
        DEL_ALL_VISITED_VIEWS:(state, view)=>{
            state.visitedviews = [];
            view.push({name:'admin'})
            // window.location.href = '/admin';
        },
        
        UPDATE_VISITED_VIEW: (state, view) => {
            for (let v of state.visitedViews) {
                if (v.path === view.path) {
                    v = Object.assign(v, view)
                    break
                }
            }
        }
    },
    actions:{//mutationsthis.$store.dispatch('action', )
        addVisitedViews({commit},view){//commit
            commit('ADD_VISITED_VIEWS',view)//ADD_VISITED_VIEWS
        },
        delVisitedViews({ commit, state },view){//promise.then
            // commit('DEL_VISITED_VIEWS',view)
            return new Promise((resolve)=>{//resolve
                commit('DEL_VISITED_VIEWS',view);
                resolve([...state.visitedviews]);
            })
        },
        delAllVisitedViews({ commit}, view){//promise.then
            // commit('DEL_VISITED_VIEWS',view)
            commit('DEL_ALL_VISITED_VIEWS', view);
            // return new Promise((resolve)=>{//resolve
            //     commit('DEL_ALL_VISITED_VIEWS');
            //     resolve([...state.visitedviews]);
            //
            // })
        },
        updateVisitedView({ commit }, view) {
            commit('UPDATE_VISITED_VIEW', view)
        }
    }
}

export default tagsview
